@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");
:root {
  --color-white: #fff;
  --color-light: #f0eff5;
  --color-gray-light: #86848c;
  --color-gray-dark: #56555e;
  --color-dark: #27282f;
  --color-primary: rgb(71, 7, 234);
  --color-success: rgb(34, 202, 75);
  --color-danger: rgb(255, 67, 54);
  --color-warning: rgb(234, 181, 7);
  --color-purple: rgb(160, 99, 245);
  --color-primary-light: rgba(71, 7, 234, 0.2);
  --color-success-light: rgba(34, 202, 75, 0.2);
  --color-danger-light: rgba(255, 67, 54, 0.2);
  --color-purple-light: rgba(160, 99, 245, 0.2);
  --card-border-radius: 1.6rem;
  --border-radius-1: 1rem;
  --border-radius-2: 6px;
  --card-padding: 1.6rem;
  --padding-1: 1rem;
  --padding-2: 8px;
}

.dark-theme {
  --color-white: #131316;
  --color-light: #23232a;
  --color-dark: #ddd;
  --color-gray-dark: #adacb5;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  appearance: none;
  border: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

html {
  font-size: 12px;
}

body {
  min-height: 100vh;
  font-family: poppins, sans-serif;
  background: var(--color-light);
  color: var(--color-dark);
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 96%;
  margin: 0 auto;
}

a {
  color: var(--color-dark);
}

img {
  max-width: 100%;
}

h1 {
  font-size: 2.2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.2rem;
}

h4 {
  font-size: 1rem;
}

h5 {
  font-size: 0.86rem;
  font-weight: 500;
}

h6 {
  font-size: 0.76rem;
}

small {
  font-size: 0.77rem;
  font-weight: 300;
}

.profile-photo {
  width: 2.8rem;
  height: 2.8rem;
  border-radius: 50%;
  overflow: hidden;
}

.text-muted {
  color: var(--color-gray-light);
}

p {
  color: var(--color-gray-dark);
  font-size: 0.86rem;
}

b {
  color: var(--color-dark);
}

.primary {
  color: var(--color-primary) !important;
}

.danger {
  color: var(--color-danger) !important;
}

.success {
  color: var(--color-success) !important;
}

.warning {
  color: var(--color-warning) !important;
}

.purple {
  color: var(--color-purple) !important;
}

.bg-primary {
  background: var(--color-primary) !important;
  box-shadow: 0 0.8rem 0.8rem var(--color-primary-light);
}

.bg-danger {
  background: var(--color-danger) !important;
  box-shadow: 0 0.8rem 0.8rem var(--color-danger-light);
}

.bg-success {
  background: var(--color-success) !important;
  box-shadow: 0 0.8rem 0.8rem var(--color-success-light);
}

.bg-purple {
  background: var(--color-purple) !important;
  box-shadow: 0 0.8rem 0.8rem var(--color-purple-light);
}

.bg-dark {
  background: var(--color-dark);
  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.2);
}

.bg-purple-light {
  background: var(--color-purple-light) !important;
}

.bg-danger-light {
  background: var(--color-danger-light) !important;
}

.bg-primary-light {
  background: var(--color-primary-light) !important;
}

.bg-success-light {
  background: var(--color-success-light) !important;
}

.material-symbols-sharp {
  font-variation-settings: "FILL" 1, "wght" 500, "GRAD" 0, "opsz" 48;
}

.catalog__list {
  display: grid;
  grid-template-columns: [one] auto [two] auto [three] auto [four] auto;
  gap: 40px;
}
.catalog__item {
  border-radius: 4px;
  overflow: hidden;
  height: 200px;
}
.catalog__item:nth-child(1) {
  grid-column: one/span 2;
}
.catalog__item:nth-child(10) {
  grid-column: three/span 2;
}
.catalog__item:nth-child(12) {
  grid-column: two/span 2;
}
.catalog__product {
  position: relative;
}
.catalog__product::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 40%, #70c05b 100%);
}
.catalog__product.is--active::after, .catalog__product:hover::after {
  background-image: linear-gradient(to bottom, transparent 0%, transparent 40%, #FF6633 100%);
}
.catalog__product-img {
  width: 100%;
}
.catalog__product-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  font-family: "Rubik-Bold";
  position: absolute;
  bottom: 15px;
  left: 10px;
  color: #fff;
  z-index: 2;
}

.pagination__list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.pagination__item {
  padding: 8px;
}
.pagination__link {
  text-decoration: none;
  color: #606060;
  font-family: "Rubik-Regular";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
}
.pagination__link:hover {
  color: #FF6633;
}
.pagination__item + .pagination__item {
  margin-left: 24px;
}

/* Navbar */
nav {
  width: 100%;
  background: var(--color-white);
  padding: 1rem 0;
}
nav img.logo {
  width: 7rem;
}
nav .search-bar {
  background: var(--color-light);
  padding: var(--padding-2) var(--card-padding);
  width: 32vw;
  border-radius: var(--border-radius-2);
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--color-gray-light);
  position: absolute;
  left: 15%;
}
nav .search-bar input[type=search] {
  color: var(--color-dark);
  background: transparent;
  width: 100%;
}
nav .search-bar input[type=search]::placeholder {
  color: var(--color-gray-dark);
}
nav .profile-area {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4rem;
}
nav .profile-area .theme-btn {
  display: flex;
  background: var(--color-light);
  width: 5rem;
  height: 2rem;
  border-radius: var(--border-radius-2);
  cursor: pointer;
  transition: all 300ms ease;
}
nav .profile-area .theme-btn span {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
}
nav .profile-area .theme-btn span.active {
  background: var(--color-dark);
  border-radius: var(--border-radius-2);
  color: var(--color-white);
}
nav .profile-area .profile {
  display: flex;
  gap: 1rem;
  align-items: center;
}
nav .profile-area .profile .profile-photo {
  display: block;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  overflow: hidden;
}
nav .profile-area button {
  display: none;
  cursor: pointer;
}

/* Aside & Sidebar */
main {
  display: grid;
  grid-template-columns: 16rem auto 30rem;
  gap: 2rem;
  width: 96%;
  margin: 1rem auto 4rem;
}
main aside {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 88vh;
}
main aside button.close-btn {
  display: none;
}
main aside .sidebar a {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  height: 4.2rem;
  color: var(--color-gray-light);
  position: relative;
}
main aside .sidebar a span {
  font-size: 1.7rem;
  margin-left: 3rem;
  transition: all 0.3s ease;
}
main aside .sidebar a.active {
  background: var(--color-white);
  color: var(--color-primary);
}
main aside .sidebar a.active::before {
  content: "";
  width: 6px;
  height: 100%;
  position: absolute;
  background: var(--color-primary);
}
main aside .sidebar a:hover {
  color: var(--color-primary);
}
main aside .sidebar a:hover span {
  margin-left: 2rem;
}
main aside .sidebar h4 {
  font-weight: 500;
}
main aside .updates {
  background: var(--color-white);
  border-radius: var(--border-radius-1);
  text-align: center;
  padding: var(--card-padding);
}
main aside .updates span {
  font-size: 2.8rem;
}
main aside .updates h4 {
  margin: 1rem 0;
}
main aside .updates a {
  display: block;
  width: 100%;
  background: var(--color-primary);
  color: var(--color-white);
  border-radius: var(--border-radius-1);
  padding: 0.8rem 0;
  margin-top: 2rem;
  transition: all 300ms ease;
}
main aside .updates a:hover {
  box-shadow: 0 1rem 2rem var(--color-primary-light);
}

/* Middle */
section.middle .header {
  display: flex;
  align-items: center;
  gap: 1rem;
}
section.middle .header input[type=date] {
  padding: 0.5rem 2rem;
  border-radius: var(--border-radius-2);
  background: var(--color-white);
  color: var(--color-gray-dark);
}
section.middle .cards {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
section.middle .cards .card {
  background: linear-gradient(#ff796f, #bd261b);
  padding: var(--card-padding);
  border-radius: var(--card-border-radius);
  color: #fff;
  height: 16rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 2rem 3rem var(--color-danger-light);
  transition: all 300ms ease;
  min-width: 22rem;
}
section.middle .cards .card:nth-child(2) {
  background: linear-gradient(#7f8191, #27282f);
  box-shadow: 0 2rem 3rem rgba(0, 0, 0, 0.2);
}
section.middle .cards .card:nth-child(3) {
  background: linear-gradient(#5d70ff, #5719c2);
  box-shadow: 0 2rem 3rem var(--color-primary-light);
}
section.middle .cards .card:hover {
  box-shadow: none;
}
section.middle .cards .card .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
section.middle .cards .card .top .left {
  display: flex;
  gap: 0.5rem;
}
section.middle .cards .card .top .left h2 {
  font-weight: 200;
  font-size: 1.4rem;
}
section.middle .cards .card .top .left img {
  width: 2.3rem;
  height: 2.3rem;
  border: 1px solid #fff;
  border-radius: var(--border-radius-2);
  padding: 0.4rem;
}
section.middle .cards .card .top img.right {
  width: 3.5rem;
}
section.middle .cards .card .middle {
  display: flex;
  justify-content: space-between;
}
section.middle .cards .card .middle .chip {
  width: 3.5rem;
}
section.middle .cards .card .bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
section.middle .cards .card .bottom .left h5 {
  text-transform: uppercase;
}
section.middle .cards .card .bottom .right {
  display: flex;
  gap: 2rem;
}

/*Monthly Report*/
.monthly-report {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
  justify-content: space-between;
  margin-top: 2rem;
}
.monthly-report h1 {
  font-weight: 700;
  font-size: 1.8rem;
}

/*Fast Payment*/
.fast-payment {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
.fast-payment .badges {
  display: flex;
  gap: 1rem;
  align-self: center;
  flex-wrap: wrap;
  max-width: 100%;
}
.fast-payment .badge {
  padding: 0.6rem var(--card-padding);
  background: var(--color-white);
  border-radius: var(--border-radius-2);
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 300ms ease;
}
.fast-payment .badge span {
  width: 7px;
  height: 7px;
  border-radius: 50%;
}
.fast-payment .badge:first-child span {
  display: flex;
  align-self: center;
  justify-content: center;
  height: fit-content;
  font-size: 1.5rem;
}
.fast-payment .badge div {
  display: flex;
  gap: 2rem;
  align-self: center;
}
.fast-payment .badge:hover {
  box-shadow: 0 0 1.5rem var(--color-primary-light);
  cursor: pointer;
}

/*Canvas*/
canvas#chart {
  background: var(--color-white);
  max-width: 100%;
  margin-top: 2rem;
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
}

/*Investments*/
.investments {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
}
.investments img {
  width: 2.4rem;
}
.investments .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--card-padding);
  padding-bottom: 0;
}
.investments .header a {
  display: flex;
  align-items: center;
  color: var(--color-primary);
}
.investments .investment {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--card-padding);
  border-top: 1px solid var(--color-light);
  transition: all 300ms ease;
}
.investments .investment:last-child {
  border: none;
}
.investments .investment:hover {
  background: var(--color-light);
  cursor: pointer;
}

/*Recent Transactions*/
.recent-transactions {
  margin-top: 2rem;
}
.recent-transactions img {
  width: 2.2rem;
}
.recent-transactions .header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.recent-transactions .header a {
  display: flex;
  align-items: center;
  color: var(--color-primary);
}
.recent-transactions .transaction {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem var(--card-padding);
  border-radius: var(--card-border-radius);
  transition: all 300ms ease;
}
.recent-transactions .transaction:hover {
  background: var(--color-white);
  cursor: pointer;
}
.recent-transactions .transaction .service {
  display: flex;
  gap: 1rem;
}
.recent-transactions .transaction .service .icon {
  padding: var(--padding-2);
  border-radius: var(--border-radius-1);
  display: flex;
  align-items: center;
}
.recent-transactions .card-details {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.recent-transactions .card-details .card {
  display: flex;
  width: 4.6rem;
  height: 3rem;
  align-items: center;
  justify-content: center;
  border-radius: var(--border-radius-2);
}

@keyframes showMenu {
  to {
    left: 0;
  }
}
/*Media Queries*/
@media screen and (max-width: 1024px) {
  nav .search-bar {
    display: none;
  }
  nav .profile-area {
    gap: 2rem;
  }
  nav .profile-area button.menu-btn {
    display: inline;
    background: transparent;
    color: var(--color-dark);
  }
  main {
    grid-template-columns: 1fr;
  }
  main aside {
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 3;
    background: var(--color-white);
    width: 22rem;
    height: 100vh;
    box-shadow: 2rem 0 2rem var(--color-primary-light);
    display: none;
    animation: showMenu 500ms ease forwards;
  }
  main aside button.close-btn {
    display: inline-block;
    width: 3rem;
    height: 3rem;
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 4;
    background: transparent;
    color: var(--color-dark);
  }
  main aside button.close-btn:hover {
    cursor: pointer;
  }
  main aside .sidebar {
    margin-top: 4rem;
  }
  main aside .updates {
    display: none;
  }
  main section.middle .cards {
    grid-template-columns: 1fr 1fr;
  }
  main canvas {
    margin: 3rem 0 1rem;
  }
  main section.right .recent-transactions {
    margin-top: 3rem;
  }
}
@media screen and (max-width: 600px) {
  nav .profile-area {
    gap: 2.6rem;
  }
  nav .profile h5,
nav .profile span {
    display: none;
  }
  main section.middle .cards {
    grid-template-columns: 1fr;
  }
  main section.middle .fast-payment {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 3rem;
    gap: 1.4rem;
  }
}